<script>
export default {
    name:'small-goods-item',
    props:{
        productName:{
            default:'加载中...',
            type:String
        }
    },
    methods:{
        toDetail(id){
            this.$router.push({
                name:'detail',
                params:{
                    id:id
                }
            })
        }
    }
}
</script>
<template>
    <div class="goods-item-small"
    @click="toDetail(123)"
    >
        <div class="goods-item-small_img">
            <img src="http://mapi.fufentong.com/tmp/uploads/20190603/d3ac79af6ab498ef076e544fa173130f.png">
            <div class="goods-item-small_img_mask"></div>
        </div>
        <div class="goods-item-small_name">{{productName}}</div>
        <div class="goods-item-small_cprice">￥<span>19.9</span></div>
        <div class="goods-item-small_oprice">￥19.9</div>
    </div>
</template>
<style lang="stylus" scoped>
.goods-item-small
    position relative
    width 2.04rem 
    &_img
        position relative
        width 2.04rem 
        height 2.04rem
        img 
            display block
            height 100%
            width 100%
        &_mask
            position absolute
    &_name 
        padding .1rem 0
        font-size .28rem
    &_cprice
        color #f07
        font-weight bold
        span
            font-size .32rem
    &_oprice
        color #999
        text-decoration line-through
</style>



