<script>
export default {
    name:'Swip'
}
</script>
<template>
    <div
    >
        <Swiper
        :duration="300" 
        dots
        autoplay
        >
            <SwiperItem key="0">
                <div class="banner">
                    <div class="banner-1"></div>
                </div>
            </SwiperItem>
            <SwiperItem key="1">
                <div class="banner">
                    <div class="banner-2"></div>
                </div>
            </SwiperItem>
            <SwiperItem key="2">
                <div class="banner">
                    <div class="banner-3"></div>
                </div>
            </SwiperItem>
        </Swiper>
    </div>
</template>
<style lang="stylus" scoped>
.banner 
    position relative
    width 7.5rem
    height 4rem
    padding 0 .25rem
.banner-1 
    background-color red
    width 100%
    height 100%
    border-radius .1rem
    z-index 3
.banner-2 
    background-color green
    width 100%
    height 100%
    border-radius .1rem
    z-index 3
.banner-3 
    background-color blue
    width 100%
    height 100%
    border-radius .1rem
    z-index 3

</style>



