<script>
import TabAd from '../public/tabad'
import GoodItemFlash from '../public/good-item-flash'
export default {
    name:'flash-sale-tab',
    components:{
        TabAd,
        GoodItemFlash
    },
    data(){
        return {
            rules:[
                '1、活动时间内用户可享受折扣价格',
                '2、秒杀商品若无质量问题不退不换',
                '3、秒杀商品退款将在2个工作日内退还至付款账户'
            ],
            times:[
                '1561939200',
                '1562025600',
                '1562112000',
                '1562198400'
            ],
            touch:{
                clientWidth:0,
                current:0,
                start:0,
                move:0,
                end:0,
                dura:300
            }
        }
    },
    methods:{
        trance_time(time){
            let date = new Date(time*1000)//unix时间戳精确到秒,浏览器时间戳精确到毫秒
            let month = date.getMonth()+1;
            let day   = date.getDate();
            let hour  = date.getHours();
            let minute= date.getMinutes();
            let strTime = {month,day,hour,minute};
            for(let i in strTime){
                strTime[i] = strTime[i]>9?strTime[i].toString():'0'+strTime[i].toString()
            }
            return strTime
        },
        stop_touch(e){

        },
        touch_start(e){
            this.touch.start = e.changedTouches[0].clientX;
            this.touch.dura = 0 ;

        },
        touch_move(e){
            this.touch.move = e.changedTouches[0].clientX-this.touch.start;
        },
        touch_end(e){
            this.touch.end = e.changedTouches[0].clientX;
            this.touch.move = 0;
            this.touch.dura = 300;
            if((this.touch.end-this.touch.start)>50){
                if(this.touch.current==0){
                    this.touch.current = 0
                }else{
                    this.touch.current-=1
                }
            }else if((this.touch.end-this.touch.start)<-50){
                if((this.touch.current+1)==this.times.length){
                     this.touch.current = (this.times.length-1)
                }else{
                    this.touch.current+=1
                }
            }
        },
        tabClick(e){
            this.touch.current = e
        }
    },
    computed:{
        transX(){
            if(Math.abs(this.touch.move)>30){
                return `translate3d(${-((this.touch.clientWidth*this.touch.current)-this.touch.move)}px, 0, 0)`
            }else{
                return `translate3d(${-((this.touch.clientWidth*this.touch.current))}px, 0, 0)`
            }
        },
        duration(){
            return `all cubic-bezier(.36,.66,.04,1) ${this.touch.dura}ms`
        }
    },
    mounted(){
        this.touch.clientWidth = this.$refs.wrap.clientWidth;
    }
}
</script>
<template>
    <div 
    class="flash-sale-tab"
    >
        <tab-ad :rules="rules"></tab-ad>
        <div class="flash-sale-classify">
            <div class="flash-sale-classify_switch"
            @touchstart.stop="stop_touch"
            @touchmove.stop="stop_touch"
            @touchend.stop="stop_touch"
            >
                <div
                v-for="(time,index) in times"
                :key="index"
                :class="['flash-sale-classify_switch__item']">
                    <div 
                    :class="['item-time',touch.current===index?'item-time__active':'']"
                    @click="tabClick(index)"
                    >
                    <span class="item-time_hour">{{trance_time(time).hour}}:{{trance_time(time).minute}}</span>
                    <span class="item-time_day">{{trance_time(time).month}}月{{trance_time(time).day}}日</span>
                    </div>
                </div>
            </div>
            <div class="flash-sale-classify_counttime">
                <div class="counttime">
                    <span class="counttime_title">距结束:</span>
                    <span class="counttime_squ">12</span>:
                    <span class="counttime_squ">12</span>:
                    <span class="counttime_squ">12</span>
                </div>
            </div>
            <div 
            :style="{
                transform:transX,
                transition:duration
                }"
            class="flash-sale-classify_container"
            ref="wrap"
            @touchstart.stop="touch_start"
            @touchmove.stop="touch_move"
            @touchend.stop="touch_end"
            >
                <div :class="['flash-sale-classify_pane',touch.current === index?'flash-sale-classify_pane__active':'']"
                v-for="(i,index) in times"
                :key="index"
                >
                    <div>
                        <template v-for="(time,key) in times">
                            <good-item-flash :key="key"></good-item-flash>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.flash-sale-classify
    position relative
    overflow hidden
    background-color #f6f6f6
    &_switch 
        transition .5s 
        position relative
        width 100%
        box-sizing border-box
        text-align center 
        white-space nowrap 
        background-color #fff
        overflow auto
        &__item 
            display inline-block
            position relative
            padding 0.1rem .25rem
            height 100%
            min-width 2.1rem
            text-overflow ellipsis
            .item-time
                display flex 
                flex-direction column
                color #666
                font-weight bold
                &_hour 
                    font-size .32rem
                &__active 
                    color #f07
    &_counttime
        padding .25rem 
        text-align right
        .counttime_title
            color #666
        .counttime_squ
            width 18px 
            height 16px 
            line-height 16px
            display inline-block
            text-align center
            font-size 11px
            border-radius 4px
            background-color #000
            color #fff
    &_container
        display block 
        white-space nowrap
        width 100%
        overflow visible
    &_pane 
        width 100% 
        padding 0 .25rem
        vertical-align top 
        white-space initial
        display inline-block
</style>




