<script>
import TabAd from '../public/tabad'
import GoodItemMid from '../public/good-item-mid'

export default {
    name:'hot-presale',
    components:{
        TabAd,
        GoodItemMid
    },
    data(){
        return {
            rules:[
                '1、预售开始时用户支付定金参与预售活动',
                '2、预售商品支付尾款后若一周内平台销售价格低于预售价用户可通过400-870-3700申请退还差价',
                '3、预售活动开始后支付定价未支付尾款的定金不退',
                '4、支付尾款后按照付款先后顺序安排发货'
            ]
        }
    }
}
</script>
<template>
    <div 
    class="hot-presale"
    >
        <tab-ad :rules="rules"></tab-ad>
        <div class="hot-presale-list">
            <template
            v-for="(i,k) in 10"
            >
                <good-item-mid :key="k"></good-item-mid>
            </template>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.hot-presale 
    position relative 
    &-list 
        display flex 
        flex-wrap wrap 
        justify-content space-between
        padding .25rem
        background-color #f6f6f6
</style>



