<script>
import rule from '../public/rules/flashsalerule'
export default {
    name:'tabad',//广告图位置
    props:{
        rules:{
            default:()=>{
                return []
            },
            type:Array
        },
        title:{
            default:'活动规则',
            type:String
        }
    },
    methods:{
        showRules(){
            this.$rules(
                {
                   rules:this.rules,
                    title:this.title 
                }
            )
        }
    }
}
</script>
<template>
    <div class="tabad">
        <img src="http://mapi.fufentong.com/tmp/uploads/20190430/2b5a649f3777176fec9a6ffdc0c793f7.jpg" alt="广告图">
        <div class="tabad-rules"
        @click='showRules'
        >
            规则<i class="iconfont icon-doubleright"></i>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.tabad
    position relative
    width 100%
    height 3.2rem
    background-color #f6f6f6
    img
        display block
        width 100% 
        height 100%
    &-rules 
        position absolute
        display inline-block
        top .2rem 
        right 0
        padding .05rem .05rem .05rem .2rem 
        border-radius .3rem 0 0 .3rem
        background-color rgba(255,255,255,.3)
        color #fff
        font-size .28rem
        .iconfont 
            font-size .28rem 
</style>



