<template>
    <div class="page-index">
        <div class="head-bg">

        </div>
        <OMTabs @change="tabChange">
            <OMTabpane 
            name="推荐" 
            :current="currentTab"
            :value="0">
                <div class="panner panner-1">
                    <Swip></Swip>
                    <div class="section">
                        <flash-sale title="限时秒杀"></flash-sale>
                    </div>
                    <div class="section">
                        <flash-sale title="低价拼团"></flash-sale>
                    </div>
                    <div class="section">
                        <flash-sale-large title="爆款预售"></flash-sale-large>
                    </div>
                    <div class="section">
                        <flash-sale-large title="1元砍价"></flash-sale-large>
                    </div>
                    <div class="section">
                        <FlashSaleHot></FlashSaleHot>
                    </div>
                </div>
            </OMTabpane>
            <OMTabpane 
            name="限时秒杀" 
            :current="currentTab"
            :value="1">
                <div class="panner">
                    <flash-sale-tab></flash-sale-tab>
                </div>
            </OMTabpane>
            <OMTabpane 
            name="低价拼团" 
            :current="currentTab"
            :value="2">
                <div class="panner">
                    低价拼团
                </div>
            </OMTabpane>
            <OMTabpane 
            name="爆款预售" 
            :current="currentTab"
            :value="3">
                <div class="panner">
                    <hot-presale></hot-presale>
                </div>
            </OMTabpane>
            <OMTabpane 
            name="1元砍价" 
            :current="currentTab"
            :value="4">
                <div class="panner">
                    1元砍价
                </div>
            </OMTabpane>
            <OMTabpane 
            name="热销爆款" 
            :current="currentTab"
            :value="5">
                <div class="panner">
                    热销爆款
                </div>
            </OMTabpane>
        </OMTabs>
    </div>
</template>
<script>
import Swip from '../components/index/swiper'
import FlashSale from '../components/index/flashsale'
import FlashSaleLarge from '../components/index/flashsalelarge'
import FlashSaleHot from '../components/index/flashsalehot'
import FlashSaleTab from '../components/tabs/flashsaletab'
import HotPresale from '../components/tabs/hotpresale'
export default {
    name:'index',
    metaInfo(){
        return {
            title:this.pageTitle+'-福分通内购'
        }
    },
    components:{
        Swip,
        FlashSale,
        FlashSaleLarge,
        FlashSaleHot,
        FlashSaleTab,
        HotPresale
    },
    data(){
        return {
            currentTab:0,
        }
    },
    computed:{
        pageTitle(){
            let titles = ['首页推荐','限时秒杀','低价拼团','爆款预售','1元砍价','热销爆款']
            if(this.$route.name==='index'){
                return titles[this.currentTab]
            }else if (this.$route.name==='detail'){
                return '商品详情'
            }else if (this.$route.name==='user'){
                return '用户中心'
            }else if(this.$route.name==='table'){
                return '提交订单'
            }
        }
    },
    methods:{
        tabChange(k){
            this.currentTab = k
        }
    }
}
</script>
<style lang="stylus" scoped>
.head-bg
    background-image url(/static/imgs/head-bg.png)
    background-size: contain;
    background-repeat no-repeat
    width 100%
    height 100vh 
    position absolute
    z-index 0
    top 0
    left 0
    background-color #f6f6f6
.panner
    position relative
    color black
    width 7.5rem
    padding-bottom 1rem
.section
    padding .25rem
    background-color #f6f6f6
</style>




