<script>
import GoodsItemSmall from '../public/good-item-small'
export default {
    name:'FlashSale',
    components:{
        GoodsItemSmall
    },
    props:{
        title:String
    }
}
</script>
<template>
    <div class="flashsale">
        <div class="flash-title">
            <div>
                <i class="flash-title-main iconfont icon-time-circle"></i>
                <span class="flash-title-main">{{title}}</span>
            </div>
            <div class="flash-title-right">
                <span>查看更多</span>
                <i class="iconfont icon-right"></i>
            </div>
        </div>
        <div class="flash-content">
            <Swiper
            :duration="300"
            dots
            activeDotColor="#ff0077"
            deactiveDotColor="#ccc"
            >
                <SwiperItem key="0">
                    <div class="goods-swiper">
                        <div class="goods-container">
                            <GoodsItemSmall></GoodsItemSmall>
                            <GoodsItemSmall></GoodsItemSmall>
                            <GoodsItemSmall></GoodsItemSmall>
                        </div>
                        <div class="goods-dots"></div>
                    </div>
                </SwiperItem>
                <SwiperItem key="1">
                    <div class="goods-swiper">
                        <div class="goods-container">
                            <GoodsItemSmall></GoodsItemSmall>
                            <GoodsItemSmall></GoodsItemSmall>
                            <GoodsItemSmall></GoodsItemSmall>
                        </div>
                        <div class="goods-dots"></div>
                    </div>
                </SwiperItem>
            </Swiper>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.flashsale
    width 100%
    background-color #fff
    border-radius .1rem
    .flash-title
        display flex 
        flex-direction row
        justify-content space-between
        height .8rem
        line-height .8rem
        padding 0 .25rem
        .icon-time-circle
            color #ff0077
        &-main 
            font-size .32rem
            font-weight bold
        &-right 
            color #999
    .flash-content 
        padding 0
        .goods-swiper 
            width 7rem
            padding 0 .25rem
            position relative
            .goods-container
                width 100%
                display flex
                justify-content space-between
            .goods-dots
                height .3rem
</style>



