<script>
export default {
    name:'good-item-large',
    props:{
        productName:{
            default:'加载中...',
            type:String
        }
    },
    methods:{
        toDetail(id){
            this.$router.push({
                name:'detail',
                params:{
                    id:id
                }
            })
        }
    }
}
</script>
<template>
    <div class="goods-item-large"
    @click="toDetail(123)"
    >
        <div class="goods-item-large_img">
            <img src="http://mapi.fufentong.com/tmp/uploads/20190617/8688b2873563cf5d10a37efdc3e5f068.jpg" alt="">
        </div>
        <div class="goods-item-large_name">
            {{productName}}
        </div>
        <div class="goods-item-large_oprice">
        ￥1999
        </div>
        <div class="goods-item-large_cprice">
            <div class="cprice">
                <span style="color:#999">定金：</span>
                ￥
                <span style="font-size:.32rem;font-weight:bold">100</span>
            </div>
            <div class="goods-item-large_tag">
                抢先预订
            </div>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.goods-item-large
    position relative
    width 5.4rem 
    &_img
        position relative
        width 5.4rem 
        height 5.4rem
        img 
            display block
            height 100%
            width 100%
    &_name 
        font-size .28rem
        padding .1rem 0
    &_oprice 
        color #f07
    &_cprice
        display flex
        justify-content space-between
        .cprice
            color #f07
    &_tag
        background linear-gradient(141deg,rgba(254,75,193,1) 0%,rgba(255,0,119,1) 100%)
        height .36rem 
        line-height .36rem
        padding 0 .12rem
        color white
        border-radius .18rem
</style>




