<script>
export default {
    name:'good-item-mid',
    methods:{
        toDetail(id){
            this.$router.push({
                name:'detail',
                params:{
                    id:id
                }
            })
        }
    }
}
</script>
<template>
    <div class="good-item-mid"
    @click="toDetail(123)"
    >
        <div class="good-item-mid_img">
            <img src="http://img13.360buyimg.com/img/jfs/t26554/309/1326769013/161306/a0283e38/5bc70812Na8099096.jpg" >
        </div>
        <div class="good-item-mid_product">
            博柏利（BURBERRY）伦敦女士香水 30ml (又名：巴宝莉伦敦女用香氛30ml）
        </div>
        <div class="good-item-mid_oprice">
            ￥325
        </div>
        <div class="good-item-mid_cprice">
            ￥<span>315</span>
        </div>
        <div class="good-item-mid_buynow">马上抢</div>
    </div>
</template>
<style lang="stylus" scoped>
.good-item-mid 
    position relative
    width 3.4rem
    background-color #fff
    border-radius .1rem
    padding-bottom .2rem
    margin-bottom .2rem 
    overflow hidden
    &_img 
        width 3.4rem
        height 3.4rem
        img 
            width 100% 
            height 100%
    &_product 
        width 100% 
        padding .2rem .2rem .1rem .2rem
        font-size .28rem 
        font-weight bold
        text-overflow ellipsis
        overflow hidden
        word-wrap none
        white-space nowrap
    &_oprice
        color #999 
        text-decoration line-through
        padding 0 .2rem
    &_cprice 
        color #f07
        padding 0 .2rem
        span 
            font-size .32rem 
            font-weight bold
    &_buynow 
        display block 
        padding .1rem .28rem 
        position absolute 
        bottom .2rem 
        right .2rem 
        font-size .32rem
        color #fff
        background linear-gradient(90deg,rgba(254,68,186,1) 0%,rgba(255,11,130,1) 100%)
        border-radius .3rem
</style>



