<script>
export default {
    name:"good-item-flash",
    data(){
        return{
            btn_type:Math.ceil(Math.random()*3),
            progress:0
        }
    },
    mounted(){
        if(this.btn_type!==3){
            this.initProgress()
        }
    },
    methods:{
        initProgress(){
            let full = this.$refs.progress.clientWidth || 100;
            this.progress = this.btn_type===2?full:Math.random()*full/2+full/2
        },
        toDetail(id){
            this.$router.push({
                name:'detail',
                params:{
                    id:id
                }
            })
        }
    }
}
</script>
<template>
    <div class="good-item-flash"
    @click="toDetail(123)"
    >
        <div class="good-item-flash_imgs">
            <div class="item-bgimg">
                <img src="" alt="">
            </div>
            <div class="item-tagimg"></div>
        </div>
        <div class="good-item-flash_profile">
            <div class="good-item-flash_title">负离子可折叠便携吹风机负离子可折叠便携吹风机</div>
            <div class="good-item-flash_rest" v-if="btn_type!==3">
                <div class="sale-progress" ref="progress">
                    <div 
                    class="sale-progress-detail"
                    :style="{width:progress+'px'}"
                    ></div>
                </div>
                <div class="sale-progress-num">还剩{{btn_type===2?0:15}}件</div>
            </div>
            <div class="good-item-flash_rest" v-else >
                <div class="sale-progress-num">限量{{15}}件</div>
            </div>
            <div class="good-item-flash_cprice">
                ￥<span style="font-size:.32rem">19.9</span>
            </div>
            <div class="good-item-flash_oprice">￥29.9</div>
            <div :class="['good-item-flash_buy','btntype_'+btn_type]">{{btn_type===1&&'马上抢'||btn_type===2&&'抢光了'||btn_type===3&&'即将开始'}}</div>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.good-item-flash
    display flex
    background-color #fff
    border-radius .1rem 
    overflow hidden
    margin-bottom .2rem
    &_imgs 
        position relative
        width 2.8rem 
        height 2.8rem 
        .item-bgimg
            width 100%
            height 100%
            img 
                display block
                width 100%
                height 100%
        .item-tagimg 
            width 100% 
            height 100% 
            position absolute
            top 0 
            left 0
    &_profile 
        flex 1 
        position relative
        padding .25rem 
    &_title 
        width 3.7rem 
        font-size .28rem 
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
        margin-bottom .25rem
    &_rest 
        display flex 
        justify-content space-between
        align-items baseline
        margin-bottom .2rem
        .sale-progress
            width 2.4rem 
            height .2rem 
            border-radius .1rem 
            background #eaeaea
            &-detail 
                height .2rem 
                border-radius .1rem 
                background  #f07
        .sale-progress-num 
            color #f07
    &_cprice 
        color #f07
        font-weight bold
        margin-bottom .1rem
    &_oprice
        color #999
        text-decoration line-through
    &_buy 
        display block 
        padding: 0.1rem 0.28rem;
        position: absolute;
        bottom: 0.2rem;
        right: 0.2rem;
        font-size: 0.32rem;
        color: #fff;
        border-radius: 0.3rem;
        &.btntype_1
            background: linear-gradient(90deg, #fe44ba 0%, #ff0b82 100%);
        &.btntype_2
            background #bfbfbf
        &.btntype_3
            background #3bb125
</style>



